.App {
    padding: 1rem;

}

.grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.product .grid {
    display: grid;
    grid-column-gap: 50px;
    grid-template-columns: 2fr 1fr;
}

.product {
    min-width: 300px;
    padding: 1rem;
    margin: 1rem 0
}
.product-name,
.cart-item {
    text-transform: capitalize;
    margin-bottom: .4rem;
}

button {
    padding: .4rem 1rem;
    margin: .4rem;
    border: none;
    cursor: pointer;
    background-color: #ccc;
}

button:hover {
    outline: thin solid yellow;
    background-color: #ddd;
}